body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #553055;
}

.App{
  background: #4c2a4c;
  margin: 20px auto;
  width: 90%;
  max-width: 600px;
  color: #eee;
}

.navbar{
  padding: 10px 20px;
  text-align: center; 
  background: #6d3d6d;
}

.navbar h1{
  width: 100%;;
}

.note-list{
  margin: 20px;
}
.note-list ul{
  padding: 0;
  list-style-type: none;
}
.note-list li{
  background: #6d3d6d;
  border-radius: 4px;
  padding: 10px;
  cursor: pointer;
  margin: 10px 0;
}

.note-list .title{
  font-weight: bold;
  color: #fff;
  font-size: 1.5em;
  margin-right: 40px;
}
.note-list .day{
  font-size: 1.2em;
  color: #ddd;
}
.empty{
  margin: 20px;
  text-align: center;
}

button{
  margin-left: 80%;
  padding:5px;
}

form{
  padding: 20px;
}
input[type='text']{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  background: #3c1f3c;
  color: #fff;
  border: 0;
}
input[type='submit']{
  margin: 10px auto;
  background: #eee;
  border: 0;
  padding: 6px 20px;
  display: block;
}